import "./App.css";
import React from "react";
import { createBrowserRouter, RouterProvider } from "react-router-dom";

import MainPage from "./Pages/MainPage";
import CreateProduct from "./Pages/CreateProductPage";
import DeleteProductPage from "./Pages/DeleteProductPage";
import EditProduct from "./Pages/EditProduct";
import CreateForm from "./Pages/CreateFormPage";
import CreateColor from "./Pages/CreateColorPage";
import DeleteForm from "./Pages/DeleteFormPage";
import DeleteColor from "./Pages/DeleteColorPage";

function App() {
  const router = createBrowserRouter([
    {
      path: "/",
      element: <MainPage />,
    },
    {
      path: "/Create_New_Product",
      element: <CreateProduct />,
    },
    {
      path: "/Delete_Product",
      element: <DeleteProductPage />,
    },
    { path: "/Edit_Product", element: <EditProduct /> },
    { path: "/Create_New_Form", element: <CreateForm /> },
    { path: "/Create_New_Color", element: <CreateColor /> },
    { path: "/Delete_Form", element: <DeleteForm /> },
    { path: "/Delete_Color", element: <DeleteColor /> },



  ]);
  return (
    <React.StrictMode>
      <RouterProvider router={router} />
    </React.StrictMode>
  );
}

export default App;
